.hymn-picker {
  .hymn-picker--content {
    height: 40vh;
    overflow: auto;
    & > div {
      padding: 1px 10px;
      border-bottom: 1px solid #ffffff20;
      border-top: 1px solid #ffffff20;
      transition: all 300ms linear;

      &.active {
        background-color: #ffffff25;
      }
    }
  }
}