/**
Page Transition
 */
.page-enter {
  transform: translate(0, -3%);
}
.page-enter-active {
  transform: translate(0, 0);
  transition: all 300ms cubic-bezier(0, 0, 0.2, 1);
}
.page-exit {
  display: none;
}
/**
Calendar Transition
 */
.months-enter {
  opacity: 0;
  visibility: hidden;
}
.months-enter-active {
  opacity: 1;
  visibility: visible;
  transition: all 500ms linear;
}
.months-exit, .months-exit-active {
  display: none;
}


@keyframes fadeIn {
  0% {
    visibility: hidden;
    opacity: 0;
    transform: translateY(-10px);
  }
  50% {
    visibility: visible;
    opacity: 0.4;
    transform: translateY(-5px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
